import { Component, OnInit } from "@angular/core";
import { NbWindowRef } from "@nebular/theme";
import { ApiService } from "../../../../services/api/api.service";
import { FormGroup, FormBuilder } from "@angular/forms";
import { Validators } from "@angular/forms";
import { UsersService } from "../../../../services/api/users.service";

@Component({
  selector: "add-tasker",
  templateUrl: "./add-tasker.component.html",
  styleUrls: ["./add-tasker.component.scss"]
})
export class AddTaskerComponent implements OnInit {
  isSubmitted = false;
  taskerForm: FormGroup;
  categories: any;

  constructor(
    public windowRef: NbWindowRef,
    private api: ApiService,
    private usersApi: UsersService,
    private builder: FormBuilder
  ) {
    // get all categories names & Ids
    this.api.getAllCategories().subscribe((res: any) => {
      this.categories = res.body;
    });

    this.taskerForm = this.builder.group({
      firstName: ["", Validators.required],
      lastName: ["", Validators.required],
      gender: ["", Validators.required],
      email: ["", [Validators.email, Validators.required]],
      password: ["", [Validators.minLength(4), Validators.required]],
      hourly: ["", Validators.required],
      categories: ["", Validators.required]
    });
  }

  addTasker() {
    this.isSubmitted = true;
    if (!this.taskerForm.valid) return false;
    let data = {
      user: this.taskerForm.value,
      hourly: this.taskerForm.value.hourly,
      categories: this.taskerForm.value.categories,
      location: {
        longtitude: 35.779261,
        latitude: -5.802842
      }
    };
    delete data.user.hourly;
    delete data.user.categories;
    this.api.addTasker(data).subscribe((res: any) => {
      console.log(res);
      this.windowRef.close();
    });
  }

  close() {
    this.windowRef.close();
  }

  ngOnInit() {}
}
