import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "../../../environments/environment";
import { catchError } from "rxjs/operators";
import { NbTokenService } from "@nebular/auth";
import { Router } from "@angular/router";
import { of } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class ApiService {
  constructor(
    private http: HttpClient,
    private token: NbTokenService,
    private router: Router
  ) {}
  getAllCategories() {
    return this.get(environment.base_url + "admin/categories");
  }
  addCategory(category: any) {
    return this.post(environment.base_url + "admin/category", category);
  }
  updateCategory(id: number, category: any) {
    return this.put(
      environment.base_url + "admin/category/update/" + id,
      category
    );
  }
  removeCategory(id: number) {
    return this.get(environment.base_url + "admin/category/delete/" + id);
  }

  getAllTasks(filter: string = "all") {
    return this.get(environment.base_url + "admin/tasks/" + filter);
  }
  removeTask(id: number) {
    return this.get(environment.base_url + "admin/task/delete/" + id);
  }
  getProfile() {
    return this.get(environment.base_url + "admin/profile");
  }
  editProfile(profile: any) {
    return this.put(environment.base_url + "admin/profile/update", profile);
  }

  getStats() {
    return this.get(environment.base_url + "stats/all");
  }

  addTasker(tasker: any) {
    return this.post(environment.base_url + "admin/tasker/add", tasker);
  }
  updateTasker(id: number, tasker: any) {
    return this.put(environment.base_url + "admin/tasker/update/" + id, tasker);
  }
  // functions

  get(url: string) {
    return this.http.get(url).pipe(
      catchError(err => {
        if (err.status === 401) this.logout();
        else {
          console.log("Response Error caught:");
          console.log(err);
        }
        return of(err);
      })
    );
  }

  post(url: string, data: any) {
    return this.http.post(url, data).pipe(
      catchError(err => {
        if (err.status === 401) this.logout();
        else {
          console.log("Response Error caught:");
          console.log(err);
        }
        return of(err);
      })
    );
  }

  put(url: string, data: any) {
    return this.http.put(url, data).pipe(
      catchError(err => {
        if (err.status === 401) this.logout();
        else {
          console.log("Response Error caught:");
          console.log(err);
        }
        return of(err);
      })
    );
  }

  delete(url: string) {
    return this.http.delete(url).pipe(
      catchError(err => {
        if (err.status === 401) this.logout();
        else {
          console.log("Response Error caught:");
          console.log(err);
        }
        return of(err);
      })
    );
  }

  logout() {
    this.token.clear();
    this.router.navigate(["auth/login"]);
  }
}
