import { ApiService } from "./../../services/api/api.service";
import { UtilsService } from "./../../services/utils/utils.service";
import { Component } from "@angular/core";
import { LocalDataSource } from "ng2-smart-table";

@Component({
  selector: "tasks-page",
  templateUrl: "./tasks.component.html"
})
export class TasksComponent {
  source: LocalDataSource;
  settings: any;
  filter: string = "all";
  constructor(private utils: UtilsService, private api: ApiService) {
    this.settings = this.utils.getTable(
      {
        id: {
          title: "ID"
        },
        title: {
          title: "Name",
          type: "string"
        },
        customerFullName: {
          title: "Customer",
          type: "string"
        },
        taskerFullName: {
          title: "Tasker",
          type: "string"
        },
        status: {
          title: "Status",
          type: "string"
        }
      },
      false,
      false
    );
    this.api.getAllTasks(this.filter).subscribe((res: any) => {
      this.source = new LocalDataSource(res.body);
      this.source.setSort([{ field: "id", direction: "asc" }]);
    });
  }

  refresh() {
    this.api.getAllTasks(this.filter).subscribe((res: any) => {
      this.source.load(res.body);
      this.source.setSort([{ field: "id", direction: "asc" }]);
    });
  }
  onCreateConfirm(event) {
    this.api.addCategory(event.newData).subscribe((res: any) => {
      if (res.errors) return;
      event.newData.id = res.data.id;
      event.confirm.resolve(event.newData);
    });
  }

  onDeleteConfirm(event: any) {
    const id = event.data.id;
    let c = confirm("Are you sure you want to delete task #" + id + " ?");
    if (!c) return;
    this.api.removeTask(id).subscribe((res: any) => {
      if (res.errors) return;
      event.confirm.resolve(event.data);
    });
  }

  filterChange(status: string) {
    this.filter = status;
    this.refresh();
  }
}
