import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NbWindowRef } from '@nebular/theme';
import { ApiService } from '../../../../services/api/api.service';
import { UsersService } from '../../../../services/api/users.service';

@Component({
  selector: 'update-customers',
  templateUrl: './update-customers.component.html',
  styleUrls: ['./update-customers.component.scss']
})
export class UpdateCustomersComponent implements OnInit {
  isSubmitted = false;
	CustomerForm : FormGroup;
  categories : any; 
  roww : any;
  
  constructor(public windowRef: NbWindowRef,private api: ApiService,private usersApi: UsersService,private builder: FormBuilder) {
    this.roww=this.windowRef.config.context; // get default values from Customer component 

    this.CustomerForm = this.builder.group({
      firstName: [this.roww.data.firstName, Validators.required],
			lastName: [this.roww.data.lastName, Validators.required],
      email: [this.roww.data.email,[Validators.email,Validators.required]]
     });
   }


   updateCustomer(){
    this.isSubmitted = true;

		if (!this.CustomerForm.valid) {
          return false;
		  } 
		else {
          console.log("****************");
          console.log(this.CustomerForm.value); 
          alert(JSON.stringify(this.CustomerForm.value));
          // here u can use updateCustomer API function 
          this.windowRef.close();  
		   } 
		}


  ngOnInit() {
  }

}
