import { ApiService } from "./../../services/api/api.service";
import { UtilsService } from "./../../services/utils/utils.service";
import { Component } from "@angular/core";
import { UsersService } from "../../services/api/users.service";
import { LocalDataSource } from "ng2-smart-table";

@Component({
  selector: "categories-page",
  templateUrl: "./categories.component.html"
})
export class CategoriesComponent {
  source: LocalDataSource;
  settings: any;
  constructor(private utils: UtilsService, private api: ApiService) {
    this.settings = {
      columns : {
        id: {
          title: "ID"
        },
        nameEn: {
          title: "Name English"
        },
        nameFr: {
          title: "Name French"
        },
        nameEs: {
          title: "Name Spanish"
        },
        nameAr: {
          title: "Name Arabic"
        }
      },
      add: {
        addButtonContent: '<i class="nb-plus"></i>',
        createButtonContent: '<i class="nb-checkmark"></i>',
        cancelButtonContent: '<i class="nb-close"></i>',
        confirmCreate: true
      },
      edit: {
        editButtonContent: '<i class="nb-edit"></i>',
        saveButtonContent: '<i class="nb-checkmark"></i>',
        cancelButtonContent: '<i class="nb-close"></i>',
        confirmEdit: true,
        confirmSave: true
      },
      delete: {
        deleteButtonContent: '<i class="nb-trash"></i>',
        confirmDelete: true
      },
      actions: {
        position: "right",
        add: true,
        edit: true
      },
      mode: "inline"
    };     
    this.api.getAllCategories().subscribe((res: any) => {
      this.source = new LocalDataSource(res.body);
    });
  }

  refresh() {
    this.api.getAllCategories().subscribe((res: any) => {
      this.source.load(res.data.clients);
    });
  }
  onCreateConfirm(event) {
    this.api.addCategory(event.newData).subscribe((res: any) => {
      if (res.errors) return;
      event.newData.id = res.body.id;
      event.confirm.resolve(event.newData);
    });
  }

  onEditConfirm(event: any) {
    let data = event.newData;
    delete data.taskers;
    delete data.tasks;

    this.api.updateCategory(event.data.id, data).subscribe((res: any) => {
      if (res.errors) return;
      event.confirm.resolve(event.newData);
    });
  }

  onDeleteConfirm(event: any) {
    const id = event.data.id;
    let c = confirm("Are you sure you want to delete category #" + id + " ?");
    if (!c) return;
    this.api.removeCategory(id).subscribe((res: any) => {
      if (res.errors) return;
      event.confirm.resolve(event.data);
    });
  }
}
