import { Tasker } from "./../../../../interfaces/users.interface";
import { Component, OnInit } from "@angular/core";
import { NbWindowRef } from "@nebular/theme";
import { UsersService } from "../../../../services/api/users.service";

@Component({
  selector: "tasker-profile",
  templateUrl: "./tasker-profile.component.html",
  styleUrls: ["./tasker-profile.component.scss"]
})
export class TaskerProfileComponent implements OnInit {
  tasker: Tasker;
  tasks: Tasker;
  row: any;
  location: Tasker;
  history: boolean = false;
  constructor(private userApi: UsersService, private windowRef: NbWindowRef) {
    this.row = this.windowRef.config.context;
    this.userApi.getOneTasker(this.row).subscribe((res: any) => {
      this.tasker = res.body.tasker;
      this.tasks = res.body.tasks;
      this.location = res.body.location;
    });
  }

  ngOnInit() {}
  showHistory() {
    this.history = true;
  }
}
