import { Component } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { NbWindowRef } from "@nebular/theme";
import { ApiService } from "../../../../services/api/api.service";

@Component({
  selector: "update-tasker",
  templateUrl: "./update-tasker.component.html",
  styleUrls: ["./update-tasker.component.scss"]
})
export class UpdateTaskerComponent {
  isSubmitted: boolean = false;
  taskerForm: FormGroup;
  categories: any;
  roww: any;
  selected: any[];

  constructor(
    public windowRef: NbWindowRef,
    private api: ApiService,
    private builder: FormBuilder
  ) {
    this.api
      .getAllCategories()
      .subscribe((res: any) => (this.categories = res.body));
    this.roww = this.windowRef.config.context;
    this.selected = this.roww.data.categoriesIDs;
    this.taskerForm = this.builder.group({
      firstName: [this.roww.data.firstName, Validators.required],
      lastName: [this.roww.data.lastName, Validators.required],
      gender: [this.roww.data.gender, Validators.required],
      email: [this.roww.data.email, [Validators.email, Validators.required]],
      hourly: [this.roww.data.hourly, Validators.required],
      categories: [this.roww.data.categoriesIDs, Validators.required] // BY ID ( array of categories id )
    });
  }

  updateTasker() {
    this.isSubmitted = true;
    if (!this.taskerForm.valid) return false;
    let data = {
      user: this.taskerForm.value,
      hourly: this.taskerForm.value.hourly,
      categories: this.taskerForm.value.categories
    };
    delete data.user.categories;
    delete data.user.hourly;
    this.api.updateTasker(this.roww.data.id, data).subscribe((res: any) => {
      console.log(res);
      this.windowRef.close();
    });
  }

  close = () => this.windowRef.close();
}
