import { UtilsService } from "./../../../services/utils/utils.service";
import { Component, TemplateRef, ViewChild } from "@angular/core";
import { UsersService } from "../../../services/api/users.service";
import { Ng2SmartTableModule, LocalDataSource } from "ng2-smart-table";
import { NbWindowService } from "@nebular/theme";
import { AddTaskerComponent } from "./add-tasker/add-tasker.component";
import { UpdateTaskerComponent } from "./update-tasker/update-tasker.component";
import { TaskerProfileComponent } from "./tasker-profile/tasker-profile.component";
@Component({
  selector: "taskers-page",
  templateUrl: "./taskers.component.html",
  styleUrls: ["taskers.style.scss"]
})
export class TaskersComponent {
  source: LocalDataSource;
  settings: any;

  constructor(
    private windowService: NbWindowService,
    private utils: UtilsService,
    private usersApi: UsersService
  ) {
    this.settings = this.utils.getTable(
      {
        id: {
          title: "ID",
          type: "number"
        },
        firstName: {
          title: "First Name",
          type: "string"
        },
        lastName: {
          title: "Last Name",
          type: "string"
        },
        email: {
          title: "Email",
          type: "string"
        },
        hourly: {
          title: "Hourly",
          type: "number"
        },
        rating: {
          title: "Rating",
          type: "number"
        }
      },
      true,
      true
    );
    this.usersApi.getAllTaskers().subscribe((res: any) => {
      this.source = new LocalDataSource(res.body);
    });
  }

  refresh() {
    this.usersApi.getAllTaskers().subscribe((res: any) => {
      this.source.load(res.data);
    });
  }

  onCreate(event) {
    let w = this.windowService.open(AddTaskerComponent, {
      title: `Add tasker`
    });
    w.onClose.subscribe(res => {
      this.refresh();
    });
  }
  onSelect(event: any) {
    this.windowService.open(TaskerProfileComponent, {
      title: "Tasker profile",
      context: event.data.id
    });
  }

  onEdit(event: any) {
    let w = this.windowService.open(UpdateTaskerComponent, {
      title: `Update tasker`,
      context: event
    });
    w.onClose.subscribe(res => {
      this.refresh();
    });
  }

  onDelete(event: any) {
    const id = event.data.id;
    let c = confirm("Are you sure you want to delete user #" + id + " ?");
    if (!c) return;
    this.usersApi.remove(id).subscribe((res: any) => {
      if (res.errors) return;
      event.confirm.resolve(event.data);
    });
  }
}
