import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { ApiService } from "../../../services/api/api.service";
import { Validators } from "@angular/forms";
import { NbToastrService } from "@nebular/theme";

@Component({
  selector: "edit-profile",
  templateUrl: "./edit-profile.component.html",
  styleUrls: ["./edit-profile.component.scss"]
})
export class EditProfileComponent implements OnInit {
  profile: any;
  profileForm: FormGroup;
  constructor(
    private api: ApiService,
    private builder: FormBuilder,
    private toastrService: NbToastrService
  ) {
    this.api.getProfile().subscribe((res: any) => {
      //this.profile = res.body.profile;
      this.profileForm = this.builder.group({
        firstName: [res.body.profile.firstName, Validators.required],
        lastName: [res.body.profile.lastName, Validators.required],
        email: [
          res.body.profile.email,
          [Validators.email, Validators.required]
        ],
        gender: [res.body.profile.gender, Validators.required]
      });
    });
  }

  ngOnInit() {}

  public update() {
    console.log("profile values : ");
    console.log(this.profileForm.value);
    this.api.editProfile(this.profileForm.value).subscribe((res: any) => {
      console.log("profile values from DB: ");
      console.log(res);
      if (res.statusCodeValue == 200) {
        this.showToast("Updated !", "top-right", "success", 3500);
        window.location.href = "#/pages/profile";
      } else {
        this.showToast(
          "Something wrong! try again",
          "top-right",
          "danger",
          3500
        );
      }
    });
  }

  showToast(title, position, status, duration) {
    this.toastrService.show(title, status || "Success", {
      position,
      status,
      duration
    });
  }
}
