import { ApiService } from "./../../services/api/api.service";
import { Component } from "@angular/core";
import { GoogleChartInterface } from "ng2-google-charts/google-charts-interfaces";
@Component({
  selector: "dashboard-page",
  templateUrl: "./dashboard.component.html"
})
export class DashboardComponent {
  stats: any;
  tasksPie: GoogleChartInterface;
  lineChart: GoogleChartInterface;
  taskersPie: GoogleChartInterface;
  tasks: boolean = true;
  constructor(private api: ApiService) {
    this.api.getStats().subscribe((res: any) => {
      this.stats = res.body;
      let dataTable = this.getDataTable();

      this.lineChart = {
        chartType: "LineChart",
        dataTable,
        options: {
          title: "Tasks per Day",

          legend: { position: "bottom" },
          height: 300,
          curveType: "function"
        }
      };

      dataTable = [["Category", "Percentage"]];
      this.stats.tasksPerCategory.forEach(element => {
        dataTable.push(element);
      });
      this.tasksPie = {
        chartType: "PieChart",
        dataTable,
        options: {
          title: "Tasks per Category",
          legend: { position: "none" }
        }
      };

      dataTable = [["Category", "Percentage"]];
      this.stats.taskersPerCategory.forEach(element => {
        dataTable.push(element);
      });
      this.taskersPie = {
        chartType: "PieChart",
        dataTable,
        options: {
          title: "Taskers per Category",
          legend: { position: "none" }
        }
      };
    });
  }
  getDataTable() {
    let dataTable: any = [["Date", "Tasks per Day"]];
    let dbRes = [];
    this.stats.tasksPerDay.forEach(element => {
      if (Object.keys(dbRes).includes(element[0].substr(0, 10)))
        dbRes[element[0].substr(0, 10)] += element[1];
      else dbRes[element[0].substr(0, 10)] = element[1];
    });

    let allDates = this.getDates(
      new Date(
        parseInt(Object.keys(dbRes)[0].substr(0, 4)),
        parseInt(Object.keys(dbRes)[0].substr(5, 2)) - 1,
        parseInt(Object.keys(dbRes)[0].substr(8, 2))
      )
    );

    allDates.forEach((date: Date) => {
      let formatted =
        date.getFullYear() +
        "-" +
        this.fixNumber(date.getMonth() + 1) +
        "-" +
        this.fixNumber(date.getDate());
      dataTable.push([
        date,
        Object.keys(dbRes).includes(formatted) ? dbRes[formatted] : 0
      ]);
    });

    return dataTable;
  }

  fixNumber = n => (n > 9 ? n : "0" + n);

  addDate(date, days) {
    date.setDate(date.getDate() + days);
    return date;
  }

  getDates(startDate) {
    var dateArray = new Array();
    var stopDate = new Date();
    var currentDate = startDate;
    while (currentDate <= stopDate) {
      dateArray.push(new Date(currentDate));
      currentDate = this.addDate(currentDate, 1);
    }
    return dateArray;
  }
}
